// 
const filterFun = (menu) => {
  for(let i=0,l=showMenus.length;i<l;i++) {
    if(showMenus[i].id == menu.id){
      menu.meta.title = showMenus[i].name;
      break;
    }
  }
}
let paths = [];
menus.forEach(menu => {
  paths.push(menu.path);
  // 
  filterFun(menu);
  
  if(menu.children){
    menu.children.forEach(c=>{
      paths.push(c.path);
      // 
      filterFun(c);
    })
  }
})
export {paths, menus};
