import vue from "@/main";
import axios from "axios";
const qs = require("qs");

// 
axios.interceptors.request.use(
  function(request) {
    // 
    request.data = request.data || {};
    // -
    if (request.url !== "/user/login") {
      const token = localStorage.getItem("token");
      token && (request.headers.token = token);
    }
    return request;
  },
  function(error) {
    vue.$alert(error, "", { type: "error" });
    return Promise.reject(error);
  }
);

// 
axios.interceptors.response.use(
  function(response) {
    // code
    /**
     * + 0 
      + 101 
      + 404 
      + 500 
      + 601 token
      + 10000 
      + 10001 OR
      + 10002 
     */
    if (response.data.code != 0) {
      const errCodeList = [101, 601];
      if (errCodeList.includes(response.data.code)) {
        vue.$router.push("/login");
        return;
      }
      vue.$alert(response.data.info, "", { type: "error" })
      return;
    }
    console.log(response.config.url, response.data); // 
    return response;
  },
  function(error) {
    vue.$alert(error, "", { type: "error" });
    return Promise.reject(error);
  }
);

export default {
  //get
  get(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: "get",
        url: process.env.VUE_APP_API_PREFIX + url,
        params: param,
      }).then(res => {
        resolve(res);
      }).catch(err => {
        reject(err);
      });
    });
  },
  //post
  post(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: "post",
        url: process.env.VUE_APP_API_PREFIX + url,
        data: param,
      }).then(res => {
        resolve(res);
      }).catch(err => {
        reject(err);
      });
    });
  },
  //form-data
  postform(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: "post",
        url: process.env.VUE_APP_API_PREFIX + url,
        data: param,
        headers: {
          "Content-Type": "multipart/form-data",
        }
      }).then(res => {
        resolve(res);
      }).catch(err => {
        reject(err);
      });
    });
  },
  put(url, param, isFormData = false) {
    let config = {
      method: "put",
      url: process.env.VUE_APP_API_PREFIX + url,
      data: param
    };
    if (isFormData) {
      config.headers = {
        "Content-Type": "application/x-www-form-urlencoded"
      };
      config.data = qs.stringify(param);
    }
    return new Promise((resolve, reject) => {
      axios(config).then(res => {
        resolve(res);
      }).catch(err => {
        reject(err);
      });
    });
  },
  // blob
  file(url, param, method = "GET") {
    return new Promise((resolve, reject) => {
      axios({
        method: method,
        url: process.env.VUE_APP_API_PREFIX + url,
        data: param,
        responseType: "blob",
        headers: {
          "Content-Type": "multipart/form-data"
        }
      }).then(res => {
        resolve(res);
      }).catch(err => {
        reject(err);
      });
    });
  },
  delete(url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: "delete",
        url: process.env.VUE_APP_API_PREFIX + url,
        params: param
      }).then(res => {
        resolve(res);
      }).catch(err => {
        reject(err);
      });
    });
  }
};
